@import '../../../shared/variables.scss';
.about {
  @extend %fonts;
  @include adaptive(padding-top, 146, 28);
  @include adaptive(padding-bottom, 90, 40);
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(286px, 1fr));
  align-items: center;
  row-gap: 50px;
  @include adaptive(column-gap, 106, 0);
  position: relative;

  &::before {
    content: '';
    position: absolute;
    opacity: 0.2;
    filter: blur(500px);
    transform: rotate(147.49deg);
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, #f43b47 0%, #453a94 100%);
  }
  &::after {
    content: '';
    position: absolute;
    filter: blur(15px);
    @include adaptive(width, 200, 100);
    @include adaptive(height, 200, 100);
    @include adaptive(right, -100, -50);
    transform: translateY(-50%);
    top: 50%;
    border-radius: 50%;
    transform: rotate(-103.81deg);
    // background: linear-gradient(90deg, #0250c5 0%, #d43f8d 100%);
    background-image: url('../../../shared/images/balls/ball-4.png');
    z-index: -1;
  }
}
.leftColumn {
}
.title {
}
.subtitle {
  @extend %subtitle;
  @include adaptive(padding-bottom, 100, 50);
}
.rightColumn {
  &::before {
    content: '';
    position: absolute;
    filter: blur(20px);
    transform: rotate(4.99deg);
    border-radius: 50%;
    @include adaptive(width, 200, 100);
    @include adaptive(height, 200, 100);
    // @include adaptive(right, -100, -50);
    transform: translateX(-50%);
    right: 50%;
    bottom: 0;
    // background: linear-gradient(90deg, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
    background-image: url('../../../shared/images/balls/ball-3.png');
    background-repeat: no-repeat;
    background-size: contain;
    z-index: -1;
  }
}
.list {
  @extend %fz-24-30;
  color: $mainColor;
  display: flex;
  flex-direction: column;
  @include adaptive(row-gap, 30, 17);
}
.item {
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(30px);
  border-radius: 2px;
  @include adaptivePadding(padding, 27, 36, 15, 20);
}
